.nav{
    position: absolute;
    left: 0;
    height: 100%;
    width: 70px;
    /* width: 200px; */
    overflow: hidden;
    background-color: #fff;
    box-shadow: 0px 0px 10px 1px rgba(0, 0, 0, 0.25);
    ul {
      width: 100%;
      height: 100%;
      display: flex;
      list-style: none;
      justify-content: flex-start;
      align-items: center;
      flex-direction: column;
      padding: 8px;
      margin: 0;
      gap: 10px;
      li {
        width: 100%;
        a {
          display: flex;
          justify-content: flex-start;
          align-items: center;
          /* background-color: #ed4444; */
          /* padding: 5px 30px; */
          padding: 5px 10px;
          text-decoration: none;
          border-radius: 12px;
          overflow: hidden;
          border: solid .5px rgba(0, 0, 0, 0);
          p {
                /* transform: scale(0); */
                position: absolute;
                margin: 0px 0 0 75px;
                color: #555;
            }
        }
        .active{
            background-color: #e2e2e2;
        }
        a:hover {
          background-color: #ebebeb;
          border: solid .5px rgba(0, 0, 0, 0.08);
          
        }
    }
}
}
.nav:hover{
    width: 200px;
    ul{
        li{
            a{
                padding: 5px 30px;
                p{
                    margin: 0px 0 0 35px;
                    /* transform: scale(1); */

                }
            }
        }
    }
  
  }

