.HomeTitle{
    position: absolute;
    top: 0;
    padding: 0 20px;
    margin: 10px 0 20px 0;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #e2e2e2;
    box-shadow: 0px 0px 10px 5px #e2e2e2;

    h1 {
        margin: 0;
        font-size: 25px;
    }

}

.feed{
    width: 500px;
    overflow-y: scroll;
    padding: 50px 10px 10px 10px;
  }
.feed > div{
    background-color: #fff;
    border-radius: 12px;
    margin: 0 0 15px 0;
    box-shadow: 0px 0px 10px 1px rgba(0, 0, 0, 0.25);
    padding: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    input {
        width: 100%;
        padding: 5px;
        margin-bottom: 10px;
    }
    button{
        width: 100%;
        padding: 5px;
        margin-bottom: 10px;
        background-color: #e2e2e2;
    }
    h3 {
        margin: 0;
    }
    img {
        width: 100%;
        background-size: contain;
    }
    p {
        align-self: flex-start;
    }
    .footer{
        width: 100%;
        display: flex;
        justify-content: space-between;
        div {
            display: flex;
            
            height: 100%;
            img{
                height: 50px;
                width: 50px;
            }
            div {
                display: flex;
                justify-content: center;
                align-items: center;
                margin-left: 10px;
                div {
                    flex-direction: column;
                    p {
                        margin: 0;
                    }
                }


            }
        }
        .sub-btn{
            justify-content: center;
            align-items: center;    
            height: auto;
            a{
                text-decoration: none;
                padding: 5px 10px;
                background-color: #ff3d3d;
                border-radius: 8px;
                p {
                    color: #e1e3e6;
                    margin: 0;
                }
            }
        }
    }
}