.menu{
    position: absolute;
    right: 0;
    top: 0;
    width: 70px;
    padding: 8px;
    height: 100%;
    background-color: #fff;
    box-shadow: 0px 0px 10px 1px rgba(0, 0, 0, 0.25);
    display: flex;
    flex-direction: column;
    .log-wrap{
        /* flex-direction: column; */
        display: flex;
        align-items: center;
        justify-content: center;
        flex-wrap: wrap;
        background-color: #e1e3e6;
        padding: 10px 10px;
        border-radius: 8px;
        overflow: hidden;
        div {
            position: relative;
            width: 100%;
            display: flex;
            justify-content: center;
            align-items: center;
            flex-direction: column;
            gap: 10px;
            
            >*{
                margin: 0;
                position: absolute;  
                top: 10px;
                right: -150px;
                p {
                    margin: 0;
                }  
            }
            a {
                padding: 10px;
                background-color: #ffffff;
                border-radius: 8px;
                text-decoration: none;
                color: #000000;
                border: solid .5px  rgba(0, 0, 0, 0.08);
            }
            a:hover {
                background-color: #c6c6c6;

            }
        }
    }
}
.menu:hover{
    width: 200px;
    .log-wrap{
        div{
            margin-top: 10px;
            >*{
                top: 0;
                right: 0px;
                margin: 0;
                position: relative;
            }
        }
    }
}